$serif: 'Source Serif Pro', serif;
$sansSerif: 'Source Sans Pro', sans-serif;

$backgroundColor: #f5f5f5;
$textColor: #666;
$titleColor: #333;
$highlightColor: #57955b;

* {
    box-sizing: border-box;
}

html, body {
    width: 100%;
    min-height: 100vh;
    margin: 0;
    padding: 0;		
    font-family: $sansSerif;
    color: $textColor;
}

h1 {
    font-variant: small-caps;
    color: $titleColor;
}

a {
   color: $titleColor; 
   text-decoration: none;
   &:hover {
       color: $highlightColor;
   }
}

.SiteHeader {
    position: relative;
    
    h1 {
        width: 100%;
        text-align: center;
        top: 40%;
        position: absolute;
        z-index: 15;
        font-family: $serif;
        font-size: 4em;
        color: white; 
    }
}

.HorizontaleMenu, .HorizontaleMenu-Background {
    width: 100%;
    position: fixed;	
    height: 50px
}

.HorizontaleMenu {
    z-index: 100;	
    border-bottom: 1px solid rgba(255,255,255,0.25);
    
    a {
        text-decoration: none;
        text-transform: uppercase;
        color: white;
        padding-left: 1em;  
    }
    
    &.Scroll {
        border-bottom: 0;
    }

    ul {
        list-style: none;
        justify-content: center;
        display: flex;
    }    
}

.HorizontaleMenu-Background {
    background-color: rgba(0,0,0,0.6);
    z-index: 9;
}
	
.wrapper, .content, main {
    display: flex;
}

.wrapper, main {
    flex-direction: column;
}		

.content main {
    background-color: $backgroundColor;
}

main {
    padding-top: 50px;
    width: 100%;
}

article {
    padding: 40px;
    background-color: white;
    
    margin: 0 auto;
    max-width: 1200px;
    width: 100%;
    position: relative;
    margin-bottom: 70px;
    font-family: $serif;
}

.BlogLayout {

    main {
        flex: 3;
    }    

    aside {
        flex: 1;
        padding: 50px 30px 95px;
    }
    
    article {
        background-color: initial;
        padding: initial;        
    }

    
    .Post {
        background: #fff;
        padding: 30px;  
        margin: auto 30px;
             
        box-shadow: 0px 3px 15px 0px rgba(0,0,0,0.15);
    }
    
    .Post.Card {
        float: left;
        z-index: 5;
        position: absolute;
        top: 30px;
        right: 0;
        bottom: 30px;
        left: 55%;
        box-shadow: 0px 3px 15px 0px rgba(0,0,0,0.65);   
        
    }

    .AlignRight .Card {
        float: right;
        left: 0;
        right: 55%;
    }

    .AlignRight .cover {
        margin-left: auto;
    }

    ul.MenuList, .MenuList ul {
        list-style: none;	
        padding: 0;	
    }

    .MenuList a:before {
        content: '\f105';
        position: absolute;
        left: 0;
        top: 0;
        font-family: 'FontAwesome';			
    }

    .MenuList a {
        display: block;
        margin: 1em 0;			
        padding: 0 0 0 15px;
        position: relative;
        margin-left: 0;
        transition: margin 0.2s ease-in-out;
        -webkit-transition: margin 0.2s ease-in-out;
    }

    .MenuList a:hover {
        margin-left: 10px;
    }

    main .cover {
        width: 960px;
        height: 540px;
        
        img {
            width: 100%;
            height: auto;
            box-shadow: 0px 3px 15px 0px rgba(0,0,0,0.15);
        }
    }

    ul.TagsCloud li {
        height: 2em;
    }

}

.TagsCloud {
    display: flex;
    flex-wrap: wrap;
    list-style: none;	
    
    a {
        color: rgba(51,51,51,0.5);
        padding: 0.2em 0.5em;	
        margin-left: 0.2em;	
        border: 1px solid #e0e0e0;;
        text-decoration: none;
        font-variant: small-caps;
        
        &:before {
            content: '#';
        }
        
        &:hover {
            color: $titleColor;
        }
    }
}

.Badge {
    &:before {
        content: ' - ';
    }
}

.HighlightLink {
    
    font-variant: small-caps;
    position: relative;
    display: inline-block;
    vertical-align: top;
    padding-left: 32px;
    transition: padding 0.2s ease-in-out;    
    
    &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 11px;
        width: 20px;
        height: 2px;
        transition: width 0.2s ease-in-out, left 0.2s ease-in-out;
        background-color: $highlightColor;
    }
    
   
    &:hover {
        padding-left: 52px;
        &:before {
            width: 40px;
        }
    }
}

.parallax-window {
    min-height: 400px;
    background: transparent;
}



time,
/*.tags,*/
.category {
    display: none;
}

@media (max-width: 1666px) {
    .BlogLayout {
        .Card {
            top: 60px;
            bottom: 0;
            left: 35%;
        }

        .AlignRight .Card {
            right: 35%;
        }	
    }
}

@media (max-width: 1200px) {

    .BlogLayout {
        main .cover {
            margin: auto;
            width: 100%;
            height: auto;
        }

        .content {
            flex-direction: column-reverse;
        }

        .Card {
            position: initial;
        }
    }
}